<template>
  <div class="select" :class="{focus, disabled}">
    <render-func v-if="label" :render="render.label">
      <label :for="id" @click="focusComponent" v-text="label"/>
    </render-func>
    <!-- <slot name="label" v-if="label" :label="label">
      <label
        v-text="label"
        :for="id"
        @click="focusComponent"
      />
    </slot> -->
    <v-menu
      ref="menu"
      transition="slide-y"
      :open="open"
      @update:open="onMenuOpenChange"
      @blur="onMenuBlur"
      @keydown.up.prevent="highlight(highlightIndex - 1)"
      @keydown.down.prevent="highlight(highlightIndex + 1)"
      @keydown.enter="onEnter"
      @opened="highlight(selected.index)"
    >
      <template v-slot:activator="menu">
        <slot v-bind="menu" :id="id">
          <div
            :id="id"
            class="input"
            role="combobox"
            v-bind="menu.aria"
            :tabindex="disabled ? -1 : 0"
            :disabled="disabled"
            @click="focus = true; menu.open($event)"
            @focus="focus = true"
            @blur="onBlur"
            @keydown.space.prevent="menu.open"
            @keydown="onKeyDown"
          >
            <slot
              name="selection"
              v-if="value && selected.item"
              v-bind="selected"
            >
              <span
                class="selection"
                v-text="selected.item[itemText]"
              />
            </slot>
            <span v-else class="placeholder" v-text="placeholder"/>
            <svg
              width="10"
              height="10"
              viewBox="0 0 10 10"
              @click.stop="menu.toggle()"
            >
              <path d="M 1,3 L 9,3 L 5,7 Z"/>
            </svg>
          </div>
        </slot>
      </template>
      <template v-slot:menu>
        <div
          class="popup-list"
          role="listbox"
          tabindex="-1"
        >
          <template v-for="(item, index) in items">
            <div
              :key="index"
              ref="item"
              tabindex="0"
              class="item"
              :class="{
                highlight: highlightIndex === index,
                selected: selected.item === item
              }"
              @click="selectItem(item)"
            >
              <slot
                name="item"
                :item="item"
                :index="index"
                :selected="selected.item === item"
              >
                <span v-text="item[itemText]"/>
              </slot>
            </div>
          </template>
        </div>
      </template>
    </v-menu>
  </div>
</template>

<script>
import VMenu from '@/components/Menu.vue'

let counter = 1

const RenderFunc = {
  functional: true,
  props: {
    render: Function
  },
  render (h, ctx) {
    if (ctx.props.render) {
      return ctx.props.render(h)
    }
    return ctx.children[0]
  }
}

export default {
  components: { VMenu, RenderFunc },
  custom: {
    label: 'label'
  },
  props: {
    value: {},
    label: String,
    placeholder: String,
    disabled: Boolean,

    items: Array,
    itemText: {
      type: String,
      default: 'text'
    },
    itemValue: {
      type: String,
      default: 'value'
    },
    open: {
      type: Boolean,
      default: undefined
    }
  },
  data () {
    return {
      id: null,
      highlightIndex: -1,
      focus: false,
      menuIsOpen: undefined
    }
  },
  computed: {
    selected () {
      const index = this.items.findIndex(item => item[this.itemValue] === this.value)
      const item = this.items[index]
      return { index, item }
    },
    render () {
      return {
        label: this.renderLabel
      }
    }
  },
  created () {
    this.id = `select_${counter++}`
  },
  methods: {
    onMenuOpenChange (value) {
      this.menuIsOpen = value
      this.$emit('update:open', value)
    },
    focusComponent () {
      if (!this.disabled) {
        this.$el.querySelector('[tabindex]').focus()
        // this.$el.firstElementChild.focus()
      }
    },
    selectItem (item) {
      this.$emit('input', this.itemValue ? item[this.itemValue] : item)
      this.focusComponent()
    },
    onBlur () {
      if (!this.menuIsOpen) {
        this.focus = false
      }
    },
    onMenuBlur () {
      this.focus = false
    },
    onEnter () {
      if (this.highlightIndex !== -1) {
        this.selectItem(this.items[this.highlightIndex])
        this.$refs.menu.closeMenu()
      }
    },
    highlight (index) {
      this.highlightIndex = Math.max(0, Math.min(this.items.length - 1, index))
      const el = this.$refs.item[this.highlightIndex]
      el && el.scrollIntoView({ block: 'nearest' })
    },
    onKeyDown (e) {
      if (!this.menuIsOpen) {
        if (e.key === 'ArrowDown' || e.key === 'ArrowUp') {
          let index = this.items.findIndex(item => item[this.itemValue] === this.value)
          const next = this.items[index + (e.key === 'ArrowDown' ? 1 : -1)]
          if (next) {
            this.$emit('input', this.itemValue ? next[this.itemValue] : next)
          }
          e.preventDefault()
        }
      }
    },
    // renderLabel () {
    //   return <label for={this.id}>xxx</label>
    // }
  }
}
</script>

