<template>
  <div class="menu" :class="{'menu-open': isOpen}">
    <slot
      name="activator"
      :toggle="toggle"
      :open="openMenu"
      :close="closeMenu"
      :aria="aria"
    />
    <portal to="popup">
      <template v-if="isOpen">
        <div
          v-if="backdrop"
          class="menu-backdrop"
          v-backdrop-scroll
        >
          <div/>
        </div>
        <document-listener
          @click="checkClickOutside"
          @keydown.esc="closeMenu"
          @keydown.tab.prevent="closeMenu"
          @keydown="$emit('keydown', $event)"
        />
      </template>
      <transition
        :name="transition"
        @enter="enter"
        @after-leave="leave"
      >
        <div
          v-if="isOpen"
          ref="menuEl"
          class="menu-content"
          :class="popupClass"
          :style="contentStyle"
          @click="closeMenu"
        >
          <slot name="menu" :is-open="isOpen" :close="closeMenu">
            <div class="popup-list">
              <slot name="menu-items"/>
            </div>
          </slot>
        </div>
      </transition>
    </portal>
  </div>
</template>

<script>
// import throttle from 'lodash/throttle'
import DocumentListener from '@/components/DocumentListener.js'
import { popupFixedPosition, pxObj } from '@/utils/popup.js'

export default {
  components: {
    DocumentListener
  },
  directives: {
    backdropScroll: {
      inserted (el) {
        el.scrollTop = 1
        el.addEventListener('scroll', () => {
          el.scrollTop = 1
        })
      }
    }
  },
  props: {
    open: {
      type: Boolean,
      default: undefined
    },
    backdrop: Boolean,
    popupClass: {
      type: [String, Array, Object],
      default: 'popup-container'
    },
    transition: {
      type: String,
      default: 'fade'
    }
  },
  computed: {
    aria () {
      return {
        'aria-expanded': Boolean(this.isOpen).toString()
      }
    }
  },
  data () {
    return {
      contentStyle: {},
      isOpen: this.open
    }
  },
  watch: {
    open (open) {
      open ? this.openMenu() : this.closeMenu()
    }
  },
  mounted () {
    const containers = []
    let el = this.$el.parentElement
    while (el) {
      if (el.classList.contains('scrollable')) {
        containers.push(el)
      }
      el = el.parentElement
    }
    // const onScroll = throttle(() => this.updatePosition(this.$refs.menuEl), 20)
    const onScroll = () => this.updatePosition(this.$refs.menuEl)
    ;[window].concat(containers).forEach(el => {
      el.addEventListener('scroll', onScroll)
    })
    this.$once('hook:beforeDestroy', () => {
      [window].concat(containers).forEach(el => {
        el.removeEventListener('scroll', onScroll)
      })
    })
    this._containers = containers
  },
  methods: {
    updatePosition (el) {
      if (!this.isOpen || !this._target || !el) {
        return
      }
      const { position, info } = popupFixedPosition(document.scrollingElement, this._target, el)
      position.minWidth = this._target.offsetWidth
      // position.maxHeight = 400
      if (!this._lastInfo || this._lastInfo.dirY !== info.dirY) {
        position.maxHeight = info.maxHeight
        this.contentStyle = pxObj(position)
      } else {
        Object.assign(this.contentStyle, pxObj(position))
      }
      this._lastInfo = info
    },
    toggle (e) {
      this.isOpen ? this.closeMenu() : this.openMenu(e)
    },
    closeMenu () {
      if (this.isOpen) {
        this.isOpen = false
        // this._target = null
        this.$emit('update:open', false)
        this.$emit('close')
      }
    },
    openMenu (e) {
      if (!this.isOpen) {
        this._target = (e && e.currentTarget) || this._target || this.$el.children[0]
        if (this.open !== false) {
          this.isOpen = true
          this.$emit('update:open', true)
          this.$emit('open')
        }
      }
    },
    enter (el) {
      this.updatePosition(el)
      this.$emit('opened')
    },
    leave () {
      this.contentStyle = {}
      this._lastInfo = null
    },
    checkClickOutside (e) {
      // e.composedPath().includes(this.$el)
      if (!this.$el.contains(e.target) && !this.$refs.menuEl.contains(e.target)) {
        this.closeMenu()
        this.$emit('blur')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.menu-content {
  position: absolute;
  z-index: 2;
  // transition: 0.05s;
  box-shadow: 0 1px 5px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12);
  background-color: #fff;
  outline: 0;
  // border-bottom-left-radius: 3px;
  // border-bottom-right-radius: 3px;
  border-radius: 3px;
  margin: 1px 0;
  max-height: 70vh;
  overflow: auto;
  display: flex;
  flex-direction: column;
}
.menu-backdrop {
  background-color: rgba(0, 0, 0, 0.12);
  position: fixed;
  top: 0;
  left: 0;
  right: -20px;
  bottom: 0;
  z-index: 1;
  overflow: auto;
  > div {
    width: 5px;
    height: 110%;
  }
}
</style>
